<template>
  <div>
    <van-nav-bar
      title="当前位置"
      left-text="返回"
      style="height: 46px"
      left-arrow
      @click-left="$router.go(-1)"
    />
    <!-- 百度地图 -->
    <baidu-map
      :center="center"
      :zoom="zoom"
      @ready="handler"
      class="buildmap"
      @click="getClickInfo"
      :scroll-wheel-zoom="true"
    >
    </baidu-map>
  </div>
</template>
<script>
export default {
  data() {
    return {
      center: { lng: 117.21081309, lat: 39.1439299 },
      zoom: 18,
    };
  },
  methods: {
    handler({ BMap, map }) {
      let geolocation = new BMap.Geolocation();

      geolocation.enableSDKLocation(); //允许SDK辅助
      geolocation.getCurrentPosition(function (r) {
        if (this.getStatus() == 0) {
          console.log(r);
          var point = new BMap.Point(r.point.lng, r.point.lat);
          map.centerAndZoom(point, 18);
            var marker = new BMap.Marker(point) // 创建标注
            map.addOverlay(marker) // 将标注添加到地图中
            var circle = new BMap.Circle(point, 2, { strokeColor: 'Red', strokeWeight: 2, strokeOpacity: 1, Color: 'Red', fillColor: '#f03' })
            map.addOverlay(circle)
        }
      });
    },
    getClickInfo(e) {
    //   this.center.lng = e.point.lng;
    //   this.center.lat = e.point.lat;
    },
  },
  created() {},
};
</script>

<style>
.buildmap {
  height: calc(100vh - 46px);
}
</style>


